'use strict';
import LjlStates from './components/changeStates';
export default {
	components: {
		LjlStates
	},
	data() {
		return{
			states: {
				index: 0,
				list: [ { title: '热门推荐', nullContent: "暂无客户" }, { title: '附近推荐', nullContent: "暂无设计师" }]
			},
		}
	},
	methods:{
		stateChange: async function({ index }) {
			this.states.index = index;
			this[`stateTo${index}`] && this[`stateTo${index}`]();
		}
	},
	async onLoad() {}
};
